import { createSlice } from '@reduxjs/toolkit';
import { addressService } from '../services/address.service';
import { openSnackBar } from './snackBarReducer';
import { getUserData } from './userReducer';

//reducers
export const addressSlice = createSlice({
    name: 'address',
    initialState: {
        gettingAddress: false,
        getAddress: false,
        addressess: {},
        address: {},
        uploading: false
    },
    reducers: {
        getRequest: (state) => {
            state.gettingAddress = true;
        },
        getSuccess: (state) => {
            state.gettingAddress = false;
            state.getAddress = true;
        },
        uploadingRequest: (state) => {
            state.uploading = true;
        },
        uploadingSuccess: (state) => {
            state.uploading = false;
        },
        setAddresses: (state, action) => {
            state.addressess = action.payload;
        },
        setAddress: (state, action) => {
            state.address = action.payload;
        }
    }
});

//actions
export const { getRequest, getSuccess, uploadingRequest, uploadingSuccess, setAddresses, setAddress } = addressSlice.actions;

export const getAddress = (id) => async (dispatch) => {
    dispatch(getRequest);
    try {
        const respData = await addressService.getAddress(id);
        dispatch(setAddresses(respData));
        dispatch(getSuccess());
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
}

export const updateAddress = (address) => (dispatch) => {
    try {
        dispatch(setAddress(address));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
}


export const saveAddress = (address) => async (dispatch) => {
    dispatch(uploadingRequest());

    try {
        await addressService.saveAddress(address);
        dispatch(uploadingSuccess());
        dispatch(getUserData(JSON.parse(localStorage.user).user.id));
        dispatch(openSnackBar({ message: `Added user address successfully.`, status: 'success' }));
    } catch (error) {
        dispatch(uploadingSuccess());
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

//selectors
export const selectAddresses = (state) => state.addressState.addresses;
export const selectAddress = (state) => state.addressState.address;
export const selectUploading = (state) => state.addressState.uploading;

export default addressSlice.reducer;